@import url('https://fonts.googleapis.com/css2?family=Qwitcher+Grypen:wght@400;700&family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&family=Roboto+Mono:ital,wght@0,100..700;1,100..700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

*{
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", serif;
}
body{
  height: 100vh;
  width: 100vw;
  overflow-y: hidden;
  background-color: #191818;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 350px;
  padding: 20px;
  background-color: #fff;
  border-radius: 5px;
}
.inner-container{
  display: flex;
  width: 100%;
  border:3px solid grey;
  padding: 5px;
  border-radius: 5px;
}
.inner-container .city-search{
  width: 100%;
  outline: none;
  border: none;
  padding: 3px;
  font-size: 1.2rem;
}
.inner-container .search-bar{
  cursor: pointer;
}
.image{
  margin-top: 25px;
  text-align: center;
}
.image img{
  box-sizing: border-box;
  width: 100px;
}